.aside_left .nav-toggler {
  position: fixed;
  top: 30px;
  left: 300px;
  z-index: 11;
  display: none;
  width: 40px;
  height: 40px;
  background-color: #fdf9ff;
  border: 1px solid #d4d4e3;
  cursor: pointer;
  transition: 0.3s;
}

.aside_left .nav-toggler span {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 2px;
}

.aside_left .nav-toggler.open span {
  background-color: transparent;
}

.aside_left .nav-toggler span::after, .aside_left .nav-toggler span::before {
  position: absolute;
  top: -6px;
  left: 0;
  width: 18px;
  height: 2px;
  content: "";
}

.aside_left .nav-toggler span::after {
  top: 6px;
}

.aside_left .nav-toggler.open span::before {
  top: 0;
  transform: rotate(45deg);
}

.aside_left .nav-toggler.open span::after {
  top: 0;
  transform: rotate(-45deg);
}

