.form-group input[type=checkbox] + .css-label::before, .mobile_sec input[type=checkbox] + .radio-label::before, .radio input[type=radio] + .radio-label::before, .style-switcher .change__thene input[type=radio] + .radio-label::before {
  position: relative;
  top: 5px;
  display: inline-block;
  width: 1.1rem;
  height: 1.1rem;
  margin-right: 1em;
  text-align: center;
  vertical-align: top;
  background: #ffffff;
  border-radius: 100%;
  cursor: pointer;
  transition: 250ms;
  content: "";
}

.style-switcher input[type=radio] + .radio-label::before {
  top: 2px;
  left: 9px;
}

.mobile_ad input[type=checkbox] + .radio-label::before {
  top: 6px;
}

.form-group input[type=checkbox] + .css-label::before {
  top: 1px;
}

.form-group input[type=checkbox]:checked + .css-label::before, .mobile_sec input[type=checkbox]:checked + .radio-label::before, .radio input[type=radio]:checked + .radio-label::before, .style-switcher input[type=radio]:checked + .radio-label::before {
  box-shadow: inset 0 0 0 4px #ffffff;
}

.form-group input[type=checkbox]:focus + .css-label::before, .mobile_sec input[type=checkbox]:focus + .radio-label::before, .radio input[type=radio]:focus + .radio-label::before, .style-switcher input[type=radio]:focus + .radio-label::before {
  outline: 0;
}

