.logo { padding: 0; }

.logo a {
  position: relative;
  display: inline-block;
  padding: 0 10px;
  color: #302e4d;
  font-weight: 700;
  font-size: 40px;
  font-family: 'Rubik', sans-serif;
  text-decoration: none;
}

.logo a::after, .logo a::before {
  position: absolute;
  width: 20px;
  height: 20px;
  content: '';
}

.logo a::before {
  top: 0;
  left: 0;
}

.logo a::after {
  right: 0;
  bottom: 0;
}

.aside_left .nav {
  margin: 80px 0 0;
  list-style: none;
}

.aside_left .nav li {
  display: block;
}

.aside_left .nav li a {
  display: block;
  color: #302e4d;
  font-weight: 600;
  font-size: 16px;
  line-height: 45px;
  text-decoration: none;
  border-bottom: 1px solid #e8dfec;
  transition: 0.3s;
}

.aside_left .nav li a:not(.active):hover {
  padding-left: 5px;
}
.copyright_text { margin-top: auto; }

.copyright_text, .copyright_text ul li a {
  color: #7d7d7d;
  font-size: 13px;
  transition: 0.3s;
}

.copyright_text ul li a:hover {
  text-decoration: none;
}

.copyright_text ul {
  padding: 0;
  list-style: none;
}
.aside_left .nav li a .fa { margin-right: 5px; }

.aside_left .nav-toggler {
  position: fixed;
  top: 30px;
  left: 300px;
  z-index: 11;
  display: none;
  width: 40px;
  height: 40px;
  background-color: #fdf9ff;
  border: 1px solid #d4d4e3;
  cursor: pointer;
  transition: 0.3s;
}

.aside_left .nav-toggler span {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 2px;
}
.aside_left .nav-toggler.open span { background-color: transparent; }

.aside_left .nav-toggler span::after, .aside_left .nav-toggler span::before {
  position: absolute;
  top: -6px;
  left: 0;
  width: 18px;
  height: 2px;
  content: '';
}

.aside_left .nav-toggler span::after {
  top: 6px;
}

.aside_left .nav-toggler.open span::before {
  top: 0;
  transform: rotate(45deg);
}

.aside_left .nav-toggler.open span::after {
  top: 0;
  transform: rotate(-45deg);
}

