.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: none;
  width: 100%;
  height: 100%;
  padding: 30px;
  background-color: rgb(
    0 0
    0 / 60%
  );
  cursor: zoom-out;
}
.lightbox.open { display: flex; }

.lightbox .lightbox-content img {
  display: block;
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 450px;
  padding: 30px 0;
  cursor: pointer;
}
.lightbox .lightbox-content { position: relative; }
.lightbox.open .lightbox-content { animation: 0.5s foo-lightboxImage; }

@keyframes foo-lightboxImage{
  0% { transform: scale(0.8); }
  100% { transform: scale(1); }
}


.lightbox .lightbox-content .lightbox-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 5px 10px;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  background-color: rgb(0 0 0 / 50%);
}

.lightbox .lightbox-content .close-lightbpx {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  color: #ffffff;
  font-size: 32px;
  line-height: 30px;
  text-align: right;
